import { Injectable } from '@angular/core';
import { map } from 'rxjs/operators';
import { of, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class DialogueService {

  DialogueData: DialogueData = {Message: 'Please Wait on', Status: 'on', Route: ''};

  constructor() { }

  GetData() {
    const DialogObservable = new Observable<DialogueData>(observer => {
      setTimeout(() => {
          observer.next(this.DialogueData);
          }, 1000);
    });
    return DialogObservable;
  }
  SetData(data: DialogueData) {
    this.DialogueData = data;
  }


}
export interface DialogueData {
  Status;
  Message;
  Route;
}
