import { LayoutModule } from '@angular/cdk/layout';
import { OverlayModule } from '@angular/cdk/overlay';
import { NgModule } from '@angular/core';

import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { HttpClientModule, HttpClient } from '@angular/common/http';
import { TranslateLoader, TranslateModule } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { CommonVariables } from './shared/commmon';
import { HTTP_INTERCEPTORS } from '@angular/common/http';

import { InterceptorService } from './shared/services/interceptor.service';
import { DialogueComponent } from './shared/commmon/dialogue/dialogue.component';
import { FormsModule } from '@angular/forms';
import { MatInputModule, MatDialogModule, MatButtonModule } from '@angular/material';

// AoT requires an exported function for factories
export const createTranslateLoader = (http: HttpClient) => {
    /* for development
    return new TranslateHttpLoader(
        http,
        '/start-javascript/sb-admin-material/master/dist/assets/i18n/',
        '.json'
    );*/
    return new TranslateHttpLoader(http, './assets/i18n/', '.json');
};

@NgModule({
    declarations: [AppComponent, DialogueComponent],
    imports: [
        BrowserModule,
        AppRoutingModule,
        BrowserAnimationsModule,
        LayoutModule,
        OverlayModule,
        HttpClientModule,
        FormsModule,
        MatInputModule,
        MatDialogModule,
        MatButtonModule,
        TranslateModule.forRoot({
            loader: {
                provide: TranslateLoader,
                useFactory: createTranslateLoader,
                deps: [HttpClient]
            }
        })
    ],
    providers: [CommonVariables, { provide: HTTP_INTERCEPTORS, useClass: InterceptorService, multi: true }],
    entryComponents: [DialogueComponent],
    bootstrap: [AppComponent]
})
export class AppModule {}
