import { Component, OnInit, Inject } from '@angular/core';
import {MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import { DialogueService } from './dialogue.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-dialogue',
  templateUrl: './dialogue.component.html',
  styleUrls: ['./dialogue.component.scss']
})

export class DialogueComponent implements OnInit {

  Message;

  constructor(
    public dialogRef: MatDialogRef<DialogueComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData, private service: DialogueService, private router: Router) {
        this.service.GetData().subscribe(Response => {
          this.Message = Response.Message;
          if (Response.Route !== '') {
            setTimeout(() => {
              this.Message = 'Please wait while we are taking you to ..';
              router.navigate([Response.Route]);
              this.dialogRef.close();
            }, 500);
          } else if (Response.Status === 'auto') {
            this.dialogRef.close();
          }

        });
    }
    ngOnInit() {
      this.Message = 'Please wait';
      setTimeout(() => {
        this.dialogRef.close();
      }, 8000);
    }



}
export interface DialogData {
  Status: string;
  Message: string;
  Route: string;
}
