import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from './shared/guard/auth.guard';
import { UserRedirectGuard } from './shared/guard/user-redirect.guard';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'login',
    pathMatch: 'full',
    canActivate: [UserRedirectGuard]
  },
  {
    path: 'crm',
    redirectTo: 'ab/crm',
    pathMatch: 'full'
  },
  {
    path: 'diet',
    redirectTo: 'ab/diet',
    pathMatch: 'full'
  },
  {
    path: 'ab',
    loadChildren: './layout/layout.module#LayoutModule',
    canActivate: [AuthGuard]
  },
  {
    path: 'login',
    loadChildren: './login/login.module#LoginModule'
  },
  {
    path: 'cs',
    loadChildren: './customer/customer.module#CustomerModule',
    canActivate: [AuthGuard]
  },
  {
    path: 'signup',
    loadChildren: './login/signup/signup.module#SignupModule'
  },
  {
    path: 'error',
    loadChildren: './error-pages/error-pages.module#ErrorPagesModule'
  },
  {
    path: '**',
    redirectTo: 'error/404',
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
  providers: [AuthGuard]
})
export class AppRoutingModule {}
