import { Injectable } from '@angular/core';
import { Observable} from 'rxjs';
import { tap } from 'rxjs/operators';
import { HttpHandler, HttpEvent, HttpRequest, HttpInterceptor, HttpResponse } from '@angular/common/http';
import { Router } from '@angular/router';
import { DialogueService } from '../commmon/dialogue/dialogue.service';
import { MatDialog } from '@angular/material';
import { DialogueComponent } from '../commmon/dialogue/dialogue.component';
@Injectable({
  providedIn: 'root'
})
export class InterceptorService implements HttpInterceptor {

  constructor(private router: Router,private loader:DialogueService,
    public dialog: MatDialog) { }

  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {

    this.showLoader();

    return next.handle(req).pipe(tap((event: HttpEvent<any>) => {
      if (event instanceof HttpResponse) {
        this.onEnd();
      }
    },
      (err: any) => {
        this.onEnd();
      }));

  }

  private onEnd(): void {
    this.hideLoader();
    
  }

  private showLoader(): void {
    this.CloseDialog();
    this.openDialog();
    this.loader.SetData({Message:"Please Wait",Status:"",Route:""});
  }

  private hideLoader(): void {
    this.loader.SetData({Message:"Please Wait",Status:"auto",Route:""});
  }

  //open dialogue
  openDialog(): void {
    this.dialog.open(DialogueComponent, {
      width: '250px',
      disableClose:true
    });
  }
  //close dialoge
  CloseDialog():void{
    this.dialog.closeAll();
  }
}
